import { Container, Icon, Image, Number } from "./styled";
import turtleImg from "img/turtle.png";
import angelImg from "img/angel.webp";
import monkeyImg from "img/monkey.webp";

const Bonus = ({ disabled, count, turtleClick, angelClick, monkeyClick }) => {
  const { turtle, angel, monkey } = count;
  return (
    <Container disabled={disabled}>
      <Icon
        count={turtle}
        onClick={turtle && turtleClick}
        disabled={disabled}
        title="Time turtle - allows you to see the near future"
      >
        <Image src={turtleImg} count={turtle} disabled={disabled} />x
        <Number>{turtle}</Number>
      </Icon>
      <Icon
        count={angel}
        onClick={angel && angelClick}
        disabled={disabled}
        title="Angel pass - allows you to skip the question"
      >
        <Image src={angelImg} count={angel} disabled={disabled} />x
        <Number>{angel}</Number>
      </Icon>
      <Icon
        count={monkey}
        onClick={monkey && monkeyClick}
        disabled={disabled}
        title="Hungry monkey - shows options for open-ended questions; 50:50 for closed-ended questions"
      >
        <Image src={monkeyImg} count={monkey} disabled={disabled} />x
        <Number>{monkey}</Number>
      </Icon>
    </Container>
  );
};

export default Bonus;
