import { Container, Clap } from "./styled";

function Claps() {
  return (
    <Container>
      {[...Array(4).keys()].map((i) => {
        const idx = i + 1;
        return (
          <Clap
            image={require(`img/claps/clap${idx}.jpg`)}
            id={idx}
            key={idx}
          />
        );
      })}
    </Container>
  );
}

export default Claps;
