import React, { useRef } from "react";
import { Player } from "./styled";
import { Question, Radio, Label } from "../styled";

const Video = ({
  question,
  options,
  video,
  value,
  onAnswerChange,
  pause,
  filled,
  showButton,
}) => {
  const videoRef = useRef(null);

  const pauseVideo = () => {
    setTimeout(() => {
      videoRef.current.pause();
      showButton();
    }, pause);
  };

  const playVideo = () => {
    videoRef.current.play();
  };

  return (
    <div>
      <Question>{question}</Question>
      <Player
        src={video}
        autoPlay
        controls
        ref={videoRef}
        onPlay={pauseVideo}
      />
      {options.map((option) => {
        return (
          <Radio key={option}>
            <input
              onChange={onAnswerChange}
              type="radio"
              value={option}
              checked={option === value}
              id={option}
            />
            <Label htmlFor={option}>{option}</Label>
          </Radio>
        );
      })}
      {filled && playVideo()}
    </div>
  );
};

export default Video;
