import { Wrapper, Container, Box, Image, Text, Title } from "./styled";
import rachel from "img/bamboozled/rachel.gif";
import monica from "img/bamboozled/monica.gif";
import phoebe from "img/bamboozled/phoebe.gif";
import joey from "img/bamboozled/joey.gif";
import chandler from "img/bamboozled/chandler.gif";
import ross from "img/bamboozled/ross.gif";

const End = () => {
  return (
    <Wrapper>
      <Container>
        <Title>You've bamboozled:</Title>
        <Box id={1}>
          <Image bg={joey} />
          <Text>Joey</Text>
        </Box>
        <Box id={2}>
          <Image bg={monica} />
          <Text>Monica</Text>
        </Box>
        <Box id={3}>
          <Image bg={phoebe} />
          <Text>Phoebe</Text>
        </Box>
        <Box id={4}>
          <Image bg={chandler} />
          <Text>Chandler</Text>
        </Box>
        <Box id={5}>
          <Image bg={rachel} />
          <Text>Rachel</Text>
        </Box>
        <Box id={6}>
          <Image bg={ross} />
          <Text>Ross</Text>
        </Box>
      </Container>
    </Wrapper>
  );
};

export default End;
