import {
  Container,
  Box,
  CardFront,
  CardBack,
  GoogleBack,
  Header,
  Description,
  Image,
} from "./styled";

const Card = ({ img, id, active, action, back, items, disabled }) => {
  return (
    <Container id={id} active={active} onClick={action} disabled={disabled}>
      <Box>
        <CardFront src={img} />
        {back && <CardBack>{back}</CardBack>}
        {items && (
          <GoogleBack>
            <Header>{items.name}</Header>
            <Description>{items.description}</Description>
            <Image src={items.gif} />
            <audio src={items.audio} autoPlay />
          </GoogleBack>
        )}
      </Box>
    </Container>
  );
};

export default Card;
