import React, { Component } from "react";
import {
  Container,
  Title,
  Rule,
  Box,
  Wheel,
  Ladder,
  MudHut,
  RainbowRing,
  GoldenMonkey,
  GoldenMonkeyGrab,
  Boom,
  ParadisePond,
  Fireworks,
} from "./styled";
import WheelOfMayhem from "components/Quiz/WheelOfMayhem/Wheel";
import Popup from "components/Popup";
import ladder from "img/ladder.png";
import mudhut from "img/mudhut.png";
import rainbowRing from "img/golden_ring.png";
import goldenMoney from "img/golden_monkey.png";
import goldenMonkeyGrab from "img/golden_monkey_grab.png";
import boom from "img/boom.png";
import paradisePond from "img/paradisePond.png";
import rules from "sounds/rules.mp3";
import fireworks from "img/fireworks.gif";
import embarrassing from "sounds/embarrassing.mp3";
import Confirmation from "./Confirmation";

class Rules extends Component {
  constructor(props) {
    super(props);
    this.audioRef = React.createRef();
    this.confirmationRef = React.createRef();
    this.state = {
      popup: false,
    };
  }

  componentDidMount() {
    setTimeout(() => this.audioRef.current.play(), 1000);
  }

  deny = () => {
    this.confirmationRef.current.play();
  };

  showPopup = () => {
    setTimeout(() => this.setState({ popup: true }), 1200);
  };

  render() {
    const { popup } = this.state;
    return (
      <Container>
        <Box>
          <Wheel>
            <WheelOfMayhem mini={true} />
          </Wheel>
          <MudHut src={mudhut} />
          <GoldenMonkey src={goldenMoney} />
          <GoldenMonkeyGrab src={goldenMonkeyGrab} />
          <Fireworks src={fireworks} left="0" />
        </Box>
        <Box>
          <Title>Rules are simple:</Title>
          <Rule id={1}>You spin the WHEEL OF MAYHEM</Rule>
          <Rule id={2}>To go up the Ladder of Chance</Rule>
          <Rule id={3}>You go past the MUD HUT</Rule>
          <Rule id={4}>Through the Rainbow Ring</Rule>
          <Rule id={5}>To get to the Golden Monkey</Rule>
          <Rule id={6}>You yank his tail and</Rule>
          <Rule id={7}>Boom!</Rule>
          <Rule id={8}>You are in paradise pond</Rule>
          <Boom src={boom} />
          <ParadisePond src={paradisePond} />
        </Box>
        <Box>
          <Ladder src={ladder} />
          <RainbowRing src={rainbowRing} />
          <Fireworks
            src={fireworks}
            right="0"
            onAnimationEnd={this.showPopup}
          />
        </Box>
        <audio src={rules} ref={this.audioRef} />
        <audio src={embarrassing} ref={this.confirmationRef} />
        {popup && (
          <Popup>
            <Confirmation deny={this.deny} />
          </Popup>
        )}
      </Container>
    );
  }
}

export default Rules;
