import styled, { keyframes } from "styled-components";
import { bounceInUp } from "react-animations";
import wheel from "img/wheelTitle.png";

export const bounceAnimation = keyframes`${bounceInUp}`;

export const Container = styled.div`
  position: relative;
  width: ${(props) => (props.mini ? "450px" : "600px")};
  height: ${(props) => (props.mini ? "450px" : "600px")};
  animation: 1s ${bounceAnimation};
  animation-fill-mode: both;

  &:before {
    content: " ";
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    z-index: 1;
    border-top: 30px solid black;
    border-left: 30px solid transparent;
    border-right: 30px solid transparent;
    border-bottom: 30px solid transparent;
  }

  @media (max-height: 800px) {
    width: ${(props) => (props.mini ? "375px" : "475px")};
    height: ${(props) => (props.mini ? "375px" : "475px")};
  }
`;

const rotate = (angle) => keyframes`
    0% {
        transform : rotate(0deg); 
    }
    100% {
        transform : rotate(${angle}deg)
    }
`;

export const WheelContainer = styled.img`
  width: 100%;
  height: 100%;
  animation: 3s ${(props) => rotate(props.rotation)};
  animation-fill-mode: forwards;
`;

export const Center = styled.div`
  position: absolute;
  width: 150px;
  height: 150px;
  background-color: black;
  border-radius: 50%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-image: url(${wheel});
  background-position: center;
  background-size: 80%;
  background-repeat: no-repeat;
`;
