import { useNavigate } from "react-router-dom";
import { Container, Boys, Apartment, End } from "./styled";
import Claps from "./Claps";
import Characters from "./Characters";
import Heads from "./Heads";
import Items from "./Items";
import boys from "img/boys.png";
import apartment from "img/apartment.jpg";
import end from "img/end.jpg";
import intro from "sounds/friends.mp3";

const Animations = () => {
  const navigate = useNavigate();

  const showQuiz = () => {
    navigate("/game/questions");
  };
  return (
    <Container>
      <Boys src={boys} />
      <Claps />
      <Characters />
      <Heads />
      <Apartment src={apartment} />
      <Items />
      <End src={end} onAnimationEnd={showQuiz} />
      <audio src={intro} autoPlay />
    </Container>
  );
};

export default Animations;
