import styled, { keyframes } from "styled-components";
import bg from "../../img/bg.jpg";
import {
  bounceInLeft,
  bounceInRight,
  bounceInUp,
  tada,
} from "react-animations";

export const Container = styled.div`
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: black;
  overflow: hidden;
`;

export const Wrapper = styled.div`
  width: 1080px;
  height: 540px;
  background-image: url(${bg});
  background-position: center;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background-size: cover;
  position: relative;
`;

const animations = [
  keyframes`${bounceInLeft}`,
  keyframes`${bounceInRight}`,
  keyframes`${bounceInUp}`,
  keyframes`${tada}`,
];

export const Text = styled.img`
  display: block;
  margin-bottom: 50px;
  animation: 0.5s ${(props) => animations[props.id]};
  animation-delay: ${(props) => props.id / 2}s;
  animation-fill-mode: both;
  margin-right: 95px;

  &:last-child {
    margin-bottom: 0;
  }
`;

export const Joey = styled.img`
  position: absolute;
  bottom: 64px;
  right: 55px;
  max-width: 300px;
  animation: 0.5s ${animations[2]}, 1.5s ${animations[3]};
  animation-delay: 1.5s, 2s;
  animation-fill-mode: both, both;
`;

export const Review = styled.img`
  position: fixed;
  bottom: 0;
  right: 25px;
  max-height: 400px;
`;
