import styled from "styled-components";
import checkmark from "img/checkmark.png";

export const Wrapper = styled.div`
  display: flex;
  width: 100%;
`;

export const Content = styled.div`
  width: 400px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin: auto;
  height: ${(props) => (props.mini ? "450px" : "600px")};

  @media (max-height: 800px) {
    height: ${(props) => (props.mini ? "375px" : "475px")};
  }
`;

export const Buttons = styled.div`
  margin-top: auto;
  margin-right: auto;
`;

export const Info = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  flex: 1;
  width: 100%;
`;

export const Title = styled.h1`
  margin: 10px auto 30px;
  font-size: 25px;
  text-align: center;
`;

export const Image = styled.img`
  max-width: 300px;
  max-height: 250px;
  margin: 0 auto 30px;
`;

export const Container = styled.div`
  display: flex;
  font-family: "Zilla Slab", serif;
  width: 100%;
  flex: 1;
`;
export const Question = styled.h1`
  font-size: 25px;
  font-family: "Internet Friends";
  margin: 10px auto;
  text-align: center;
`;

export const Label = styled.label`
  display: block;
  padding-left: 25px;
  position: relative;
  cursor: pointer;
  display: flex;
  align-items: center;
  font-size: 20px;

  &:before {
    content: " ";
    width: 15px;
    height: 15px;
    border: 1px solid black;
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
  }
`;

export const Radio = styled.div`
  display: block;
  line-height: 1;
  margin-bottom: 10px;

  input {
    display: none;

    &:disabled {
      & + ${Label} {
        opacity: 0.5;
      }
    }

    &:checked {
      & + ${Label} {
        &:after {
          content: " ";
          position: absolute;
          width: 15px;
          height: 15px;
          top: 50%;
          transform: translateY(-50%);
          left: 0;
          display: flex;
          justify-content: center;
          align-items: center;
          text-align: center;
          background-color: #00b4e9;
          color: white;
          font-size: 14px;
          border: 1px solid #00b4e9;
          background-image: url(${checkmark});
          background-size: contain;
          background-repeat: no-repeat;
          background-position: center;
        }
      }
    }
  }
`;

export const Answer = styled.img`
  max-width: 200px;
  position: absolute;
  bottom: 30px;
  right: 30px;
  pointer-events: none;
  border: 2px solid white;
  border-radius: 15px;
`;
