import styled from "styled-components";

export const Container = styled.div`
  display: flex;
  flex-wrap: wrap;
`;

export const Question = styled.div`
  width: 50%;
  margin-bottom: 15px;
  position: relative;

  @media (max-height: 800px) {
    width: 33.33%;
    margin-bottom: 10px;
    padding: 5px;
  }
`;

export const Text = styled.p`
  font-weight: bold;
  font-size: 16px;
  margin: 0 0 15px;

  @media (max-height: 800px) {
    font-size: 15px;
    margin: 0 0 10px;
  }
`;
