import { Component } from "react";
import {
  Wrapper,
  Content,
  Buttons,
  Info,
  Title,
  Image,
  Answer,
} from "./styled";
import Wheel from "./Wheel";
import Button from "components/Button";
import Sound from "./Sound";
import Picture from "./Picture";
import Quotation from "./Quotation";
import Video from "./Video";
import spinLabel from "img/spin.png";
import backLabel from "img/back.png";
import answerLabel from "img/answer.png";
import { audioQuestions } from "data/audioQuestions";
import { pictureQuestions } from "data/pictureQuestions";
import { quotationQuestions } from "data/quotationQuestion";
import { videoQuestions } from "data/videoQuestions";

const questionMap = new Map([
  ["audio", audioQuestions],
  ["picture", pictureQuestions],
  ["quotation", quotationQuestions],
  ["video", videoQuestions],
]);

const componentMap = new Map([
  ["audio", Sound],
  ["picture", Picture],
  ["quotation", Quotation],
  ["video", Video],
]);

const right = [
  require("img/right/1.gif"),
  require("img/right/2.gif"),
  require("img/right/3.gif"),
  require("img/right/4.gif"),
  require("img/right/5.gif"),
  require("img/right/6.gif"),
  require("img/right/7.gif"),
  require("img/right/8.gif"),
  require("img/right/9.gif"),
  require("img/right/10.gif"),
  require("img/right/11.gif"),
  require("img/right/12.gif"),
  require("img/right/13.gif"),
  require("img/right/14.gif"),
];

const wrong = [
  require("img/wrong/1.gif"),
  require("img/wrong/2.gif"),
  require("img/wrong/3.gif"),
  require("img/wrong/4.gif"),
  require("img/wrong/5.gif"),
  require("img/wrong/6.gif"),
  require("img/wrong/7.gif"),
  require("img/wrong/8.gif"),
  require("img/wrong/9.gif"),
  require("img/wrong/10.gif"),
  require("img/wrong/11.gif"),
  require("img/wrong/12.gif"),
  require("img/wrong/13.gif"),
];

const random = (min, max) => Math.floor(Math.random() * (max - min)) + min;

class WheelOfMayhem extends Component {
  constructor(props) {
    super(props);

    this.state = {
      spinned: false,
      animated: false,
      back: false,
      showAnswerButton: false,
      value: "",
      correct: false,
      filled: false,
      showButton: false,
      yes: right[random(0, 14)],
      no: wrong[random(0, 13)],
    };
  }

  spin = () => {
    this.props.onClick();

    this.setState({
      spinned: true,
    });
  };

  onAnswerChange = (e) => {
    this.setState({
      value: e.target.value,
    });
  };

  showBack = () => {
    const { data } = this.props;

    if (data.questionType === "") {
      this.setState({
        back: true,
        animated: true,
        showButton: false,
      });
    } else {
      this.setState({
        showQuestion: true,
        animated: true,
        showButton: false,
      });
    }
  };

  validate = () => {
    const { data } = this.props;
    const { questionType } = data;
    const { value } = this.state;
    const question = questionMap.get(questionType)[data[questionType]];

    if (question.answer.toLowerCase() === value.toLowerCase()) {
      this.setState({
        correct: true,
        filled: true,
        showButton: false,
      });
    } else {
      this.setState({
        correct: false,
        filled: true,
      });
    }
  };

  showButton = () => {
    this.setState({ showButton: true });
  };

  render() {
    const { rotation, audio, mini, data, goBack } = this.props;
    const {
      spinned,
      animated,
      value,
      filled,
      correct,
      showButton,
      yes,
      no,
    } = this.state;
    const { message, gif, sound, questionType } = data;
    let currentQuestion, QuestionComponent;

    if (questionType) {
      currentQuestion = questionMap.get(questionType)[data[questionType]];
      QuestionComponent = componentMap.get(questionType);
    }

    return (
      <Wrapper>
        <Wheel mini={mini} rotation={rotation} audio={audio} />
        <Content>
          <Info>
            {spinned && !animated && (
              <>
                <Title>{message}</Title>
                <Image src={gif} />
                <audio src={sound} autoPlay onEnded={this.showBack} />
              </>
            )}
            {animated && questionType !== "" && (
              <>
                {currentQuestion && QuestionComponent && (
                  <QuestionComponent
                    {...currentQuestion}
                    value={value}
                    onAnswerChange={this.onAnswerChange}
                    filled={filled}
                    showButton={this.showButton}
                  />
                )}
              </>
            )}
          </Info>
          {filled && correct && <Answer src={yes} />}
          {filled && !correct && <Answer src={no} />}
          <Buttons>
            {!spinned && <Button label={spinLabel} action={this.spin} />}
            {((animated && questionType === "") || (filled && correct)) && (
              <Button label={backLabel} action={() => goBack(questionType)} />
            )}
            {showButton && (
              <Button label={answerLabel} action={this.validate} />
            )}
          </Buttons>
        </Content>
      </Wrapper>
    );
  }
}

export default WheelOfMayhem;
