import styled from "styled-components";

export const BigImage = styled.img`
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: 600px;
  max-height: 600px;
  display: none;
`;

export const Container = styled.div`
  position: relative;

  &:hover {
    ${BigImage} {
      display: block;
    }
  }
`;

export const Image = styled.img`
  max-width: 300px;
  max-height: 150px;
  margin: 20px auto;
`;
