import styled from "styled-components";

export const Wrapper = styled.div`
  width: 100%;
`;

export const Container = styled.div`
  display: flex;
  flex-wrap: wrap;
`;

export const Friend = styled.input`
  background-image: url(${(props) => props.bg});
  height: 140px;
  width: 30%;
  margin-right: 3%;
  margin-bottom: 10px;
  background-repeat: no-repeat;
  background-position: top;
  background-size: cover;
  cursor: pointer;
  border: ${(props) => (props.selected ? "2px solid #00B4E9" : "none")};
  color: transparent;
  outline: none;
  font-size: 0;
`;
