import { Container, WheelContainer, Center } from "./styled";
import wheel from "img/wheel.png";
import sound from "sounds/wheel.mp3";

const Wheel = ({ rotation, audio, mini }) => {
  return (
    <Container mini={mini}>
      <WheelContainer rotation={rotation} src={wheel} />
      <Center />
      <audio src={sound} ref={audio} />
    </Container>
  );
};

export default Wheel;
