import {
  Container,
  Content,
  Field,
  ImageContent,
  Text,
  Input,
  Radio,
  Label,
  Options,
  Image,
  ButtonContainer,
} from "./styled";
import Button from "components/Button";
import label from "img/answer.png";

const Question = ({
  query,
  onAnswerChange,
  value,
  validate,
  showPicture,
  hungryMonkey,
  enabledOptions,
  error,
  animationEnd,
  handleKeyDown,
  showButton,
}) => {
  const { question, answer, options, open, picture } = query;
  const showOptions = !open || hungryMonkey;
  const possibilities = hungryMonkey && !open ? enabledOptions : options;
  return (
    <Container>
      <Content>
        <Text>{question}</Text>
        <Field>
          {open && (
            <Input
              onChange={onAnswerChange}
              value={value}
              error={error}
              onAnimationEnd={animationEnd}
              onKeyDown={(e) => handleKeyDown(e, answer)}
            />
          )}
          {showOptions && (
            <Options error={error} onAnimationEnd={animationEnd}>
              {options.map((o, i) => {
                return (
                  <Radio key={o}>
                    <input
                      onChange={onAnswerChange}
                      type="radio"
                      value={o}
                      checked={o === value}
                      id={o}
                      disabled={possibilities.indexOf(o) === -1}
                    />
                    <Label htmlFor={o}>{o}</Label>
                  </Radio>
                );
              })}
            </Options>
          )}
        </Field>
        <ButtonContainer>
          {showButton && (
            <Button label={label} action={() => validate(answer)} />
          )}
        </ButtonContainer>
      </Content>
      <ImageContent>{showPicture && <Image src={picture} />}</ImageContent>
    </Container>
  );
};

export default Question;
