import styled from "styled-components";

export const Container = styled.div`
  display: flex;
  font-size: 13px;
  pointer-events: ${(props) => (props.disabled ? "none" : "auto")};
  position: absolute;
  right: 50px;
  bottom: 30px;
`;
export const Icon = styled.div`
  width: 33.33%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  cursor: ${(props) =>
    props.count > 0 && !props.disabled ? "pointer" : "default"};
`;

export const Image = styled.img`
  max-width: 75px;
  max-height: 75px;
  filter: grayscale(
    ${(props) => (props.count > 0 && !props.disabled ? "0%" : "100%")}
  );
`;

export const Number = styled.div`
  font-size: 20px;
`;
