import styled, { keyframes } from "styled-components";
import { fadeIn, fadeOut } from "react-animations";

const fadeInAnimation = keyframes`${fadeIn}`;
const fadeOutAnimation = keyframes`${fadeOut}`;

export const Container = styled.div`
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
`;

export const Item = styled.img`
  position: absolute;
  max-width: 200px;
  animation: 0.5s ${fadeInAnimation}, 0.5s ${fadeOutAnimation};
  animation-fill-mode: backwards, forwards;

  &:nth-child(1) {
    top: 150px;
    right: 50px;
    animation-delay: 36.5s, 42s;
  }

  &:nth-child(2) {
    bottom: 50px;
    left: 75px;
    animation-delay: 37.5s, 42s;
  }

  &:nth-child(3) {
    top: 300px;
    left: 500px;
    animation-delay: 38.5s, 42s;
  }

  &:nth-child(4) {
    bottom: 50px;
    right: 225px;
    animation-delay: 39.5s, 42s;
  }

  &:nth-child(5) {
    top: 175px;
    left: 200px;
    animation-delay: 40.5s, 42s;
  }
`;
