import styled from "styled-components";

export const Label = styled.label`
  display: block;
  padding-left: 25px;
  position: relative;
  cursor: pointer;
  display: flex;
  align-items: center;

  &:before {
    content: " ";
    width: 15px;
    height: 15px;
    border: 1px solid black;
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
  }
`;

export const Input = styled.div`
  display: block;
  line-height: 1;
  margin-bottom: 10px;

  input {
    display: none;
    &:checked {
      & + ${Label} {
        &:after {
          content: "${(props) => props.letter}";
          position: absolute;
          width: 15px;
          height: 15px;
          top: 50%;
          transform: translateY(-50%);
          left: 0;
          display: flex;
          justify-content: center;
          align-items: center;
          text-align: center;
          background-color: #00b4e9;
          color: white;
          font-size: 14px;
          border: 1px solid #00b4e9;
        }
      }
    }
  }
`;
