import styled, { keyframes } from "styled-components";
import { bounceInUp, bounceOutDown } from "react-animations";

const bounceInUpAnimation = keyframes`${bounceInUp}`;
const bounceOutDownAnimation = keyframes`${bounceOutDown}`;

export const Container = styled.div`
  position: absolute;
  bottom: -5px;
  z-index: 30;
  margin: auto;
  animation: 1s ${bounceInUpAnimation}, 0.5s ${bounceOutDownAnimation};
  animation-delay: 0s, 1.5s;
  animation-fill-mode: backwards, forwards;
  width: calc(100% - 50px);
`;

export const Image = styled.img`
  max-height: 600px;
  margin: auto;
  display: block;
`;

export const Text = styled.p`
  position: absolute;
  top: ${(props) => props.position.top};
  left: ${(props) => props.position.left};
  transform: rotate(${(props) => props.position.rotation});
  text-align: center;
  font-size: 25px;
`;
