import styled, { keyframes } from "styled-components";
import {
  bounceInUp,
  tada,
  hinge,
  rollIn,
  pulse,
  rollOut,
  fadeIn,
} from "react-animations";

const bounceInUpAnimation = keyframes`${bounceInUp}`;
const tadaAnimation = keyframes`${tada}`;
const hingeAnimation = keyframes`${hinge}`;
const fadeInAnimation = keyframes`${fadeIn}`;
const rollInAnimation = keyframes`${rollIn}`;
const pulseAnimation = keyframes`${pulse}`;
const rollOutAnimation = keyframes`${rollOut}`;

const slide = keyframes`
  from {
    right: 0;
  }

  to {
    right: 688px;
  }
`;

export const Container = styled.div`
  overflow: hidden;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
`;

export const Boys = styled.img`
  position: absolute;
  right: 0;
  bottom: 0;
  animation: 1s ${bounceInUpAnimation}, 1s ${tadaAnimation}, 5s ${slide},
    1s ${hingeAnimation};
  animation-delay: 2s, 3.5s, 3.5s, 8s;
  animation-iteration-count: 1, 4, 1, 1;
  animation-fill-mode: both, forwards, forwards, both;
`;

export const Apartment = styled.img`
  width: 750px;
  margin: 150px auto 50px;
  height: auto;
  display: block;
  animation: 1s ${rollInAnimation}, 1s ${pulseAnimation}, 1s ${rollOutAnimation};
  animation-delay: 29.5s, 30.5s, 35.5s;
  animation-iteration-count: 1, 5, 1;
  animation-fill-mode: backwards, none, forwards;
`;

export const End = styled.img`
  animation: 3s ${fadeInAnimation};
  animation-delay: 42s;
  animation-fill-mode: backwards;
  position: absolute;
  top: 135px;
  left: 50%;
  transform: translateX(-50%);
  width: 800px;
  height: auto;
  z-index: 10;
`;
