import styled from "styled-components";

export const Container = styled.div`
  padding: 20px;
  min-width: 250px;
  border: 1px solid #eb1119;
  border-radius: 10px;
  position: relative;
  cursor: pointer;
  background-color: rgba(255, 255, 255, 0.6);
  z-index: 10;

  &:before {
    content: " ";
    position: absolute;
    top: 3px;
    left: 3px;
    border: 1px solid #00b4e9;
    width: 100%;
    height: 100%;
    border-radius: 10px;
    pointer-events: none;
  }

  &:after {
    content: " ";
    position: absolute;
    top: -5px;
    left: -5px;
    border: 1px solid #fabe07;
    width: 100%;
    height: 100%;
    border-radius: 10px;
    pointer-events: none;
  }
`;

export const Label = styled.img`
  max-height: 30px;
  margin: auto;
  display: block;
  pointer-events: none;
  cursor: pointer;
`;
