import { Input, Label } from "./styled";

function Radiobutton({ name, onAnswerChange, options, id, answers }) {
  return (
    <Input letter={name}>
      <input
        onChange={onAnswerChange}
        type="radio"
        value={options[name]}
        name={`question${id}`}
        checked={answers[`question${id}`] === options[name]}
        id={options[name]}
      />
      <Label htmlFor={options[name]} name={`question${id}`}>
        {options[name]}
      </Label>
    </Input>
  );
}

export default Radiobutton;
