import styled, { keyframes } from "styled-components";
import {
  wobble,
  fadeInLeft,
  rollIn,
  rollOut,
  bounceInUp,
  bounceOutDown,
  fadeIn,
  fadeOut,
  rubberBand,
  zoomIn,
  zoomOut,
  tada,
} from "react-animations";

const wobbleAnimation = keyframes`${wobble}`;
const fadeInLeftAnimation = keyframes`${fadeInLeft}`;
const rollInAnimation = keyframes`${rollIn}`;
const rollOutAnimation = keyframes`${rollOut}`;
const bounceInUpAnimation = keyframes`${bounceInUp}`;
const bounceOutDownAnimation = keyframes`${bounceOutDown}`;
const fadeInAnimation = keyframes`${fadeIn}`;
const fadeOutAnimation = keyframes`${fadeOut}`;
const rubberBandAnimation = keyframes`${rubberBand}`;
const zoomInAnimation = keyframes`${zoomIn}`;
const zoomOutAnimation = keyframes`${zoomOut}`;
const tadaAnimation = keyframes`${tada}`;

export const Container = styled.div`
  display: flex;
  align-items: center;
`;

export const Box = styled.div`
  width: 33.33%;
  text-align: center;
  position: relative;
`;

export const Title = styled.h1`
  animation: 1s ${wobbleAnimation};
  animation-delay: 0.5s;
  margin-bottom: 50px;
`;

export const Rule = styled.p`
  animation: 0.5s ${fadeInLeftAnimation};
  animation-delay: ${(props) => props.id}s;
  animation-fill-mode: both;
`;

export const Wheel = styled.div`
  animation: 0.5s ${rollInAnimation}, 0.5s ${rollOutAnimation};
  animation-delay: 1.5s, 2s;
  animation-fill-mode: backwards, forwards;
`;

export const Ladder = styled.img`
  animation: 0.5s ${bounceInUpAnimation}, 0.5s ${bounceOutDownAnimation};
  animation-delay: 2.25s, 2.75s;
  animation-fill-mode: backwards, forwards;
  max-height: 450px;
`;

export const MudHut = styled.img`
  max-width: 350px;
  position: absolute;
  top: 50px;
  left: 0;
  animation: 0.25s ${fadeInAnimation}, 0.5s ${rubberBandAnimation},
    0.25s ${fadeOutAnimation};
  animation-delay: 3.5s, 3.75s, 4s;
  animation-fill-mode: backwards, both, forwards;
`;

export const RainbowRing = styled.img`
  position: absolute;
  top: 50px;
  left: 0;
  max-width: 350px;
  animation: 0.5s ${zoomInAnimation}, 0.5s ${zoomOutAnimation};
  animation-delay: 4.25s, 5s;
  animation-fill-mode: backwards, forwards;
`;

export const GoldenMonkey = styled.img`
  position: absolute;
  top: 0;
  left: 0;
  max-width: 350px;
  animation: 0.25s ${fadeInAnimation}, 0.1s ${fadeOutAnimation};
  animation-delay: 5.25s, 6s;
  animation-fill-mode: backwards, forwards;
`;

export const GoldenMonkeyGrab = styled.img`
  position: absolute;
  top: 0;
  left: 0;
  max-width: 350px;
  animation: 0.1s ${fadeInAnimation}, 0.5s ${tadaAnimation},
    0.25s ${fadeOutAnimation};
  animation-delay: 5.8s, 6.5s, 7s;
  animation-fill-mode: backwards, both, forwards;
`;

export const Boom = styled.img`
  position: absolute;
  top: 0;
  left: -100px;
  max-width: 600px;
  animation: 0.25s ${zoomInAnimation}, 0.5s ${tadaAnimation},
    0.25s ${zoomOutAnimation};
  animation-delay: 7.25s, 7.5s, 7.75s;
  animation-fill-mode: backwards, both, forwards;
`;

export const ParadisePond = styled.img`
  position: absolute;
  top: -50px;
  left: -200px;
  max-width: 900px;
  animation: 0.5s ${fadeInAnimation}, 0.5s ${fadeOutAnimation};
  animation-delay: 8s, 10s;
  animation-fill-mode: backwards, forwards;
`;

export const Fireworks = styled.img`
  position: absolute;
  top: 0;
  left: ${(props) => props.left || "auto"};
  right: ${(props) => props.right || "auto"};
  animation: 0.25s ${fadeInAnimation}, 0.25s ${fadeOutAnimation};
  animation-delay: 8s, 10s;
  animation-fill-mode: backwards, forwards;
  z-index: 3;
`;
