import React, { Component } from "react";
import { Navigate } from "react-router-dom";
import { Container } from "./styled";
import WheelOfMayhem from "./WheelOfMayhem";
import Cards from "./Cards";
import Question from "./Question";
import Bonus from "./Bonus";
import Bikini from "./Bikini";
import { questions } from "data/quiz";

const random = (min, max) => Math.floor(Math.random() * (max - min)) + min;
const getEnabledOptions = (options, answer) => {
  const answers = [...options];
  while (answers.length > 3) {
    const index = random(0, 6);
    if (answers[index] !== answer) {
      answers.splice(index, 1);
    }
    getEnabledOptions(answers, answer);
  }
  return answers;
};

const levels = [
  "Ladder of chance",
  "Mud Hut",
  "Rainbow Ring",
  "Golden Monkey",
  "yanking Golden Monkey tail",
  "Paradise Pond",
];

const googleCards = [
  {
    name: "Gimme Card",
    description: "You steal Ross' points",
    gif: require("img/google/ross.gif"),
    audio: require("sounds/googleRoss.mp3"),
  },
  {
    name: "Wise Advice",
    description: "You can talk to Chandler",
    gif: require("img/google/chandler.gif"),
    audio: require("sounds/googleChandler.mp3"),
  },
  {
    name: "Cringy bingy",
    description: "You made Rachel wear sandals and socks",
    gif: require("img/google/rachel.gif"),
    audio: require("sounds/googleRachel.mp3"),
  },
  {
    name: "Disaster master",
    description: "Monica loses apartment because of you",
    gif: require("img/google/monica.gif"),
    audio: require("sounds/googleMonica.mp3"),
  },
  {
    name: "Helping hands",
    description: "Phoebe can help you with the next question",
    gif: require("img/google/phoebe.gif"),
    audio: require("sounds/googlePhoebe.mp3"),
  },
  {
    name: "Nasty Thief",
    description: "You can steal Joey's sandwich",
    gif: require("img/google/joey.gif"),
    audio: require("sounds/googleJoey.mp3"),
  },
];

class Quiz extends Component {
  constructor(props) {
    super(props);
    this.audioRef = React.createRef();
    this.state = {
      navigate: false,
      rotation: 0,
      wheel: false,
      cards: false,
      value: "",
      result: false,
      filled: false,
      bonuses: {
        turtle: 2,
        angel: 2,
        monkey: 2,
      },
      question: 0,
      gif: false,
      disableBonuses: false,
      hungryMonkey: false,
      enabledOptions: [],
      points: 0,
      showPoints: false,
      girlIdx: 0,
      level: 0,
      showButton: true,
      wheelOfMayhem: {
        message: "",
        audio: 0,
        picture: 0,
        quotation: 0,
        video: 0,
        questionType: "",
        sound: "",
        gif: "",
      },
    };
  }

  changeLevel = () => {
    let { level } = this.state;
    let message = "";
    const result = random(0, 2);

    if (result === 0) {
      if (level > 0) {
        level = level - 1;
        message = `You go lower to ${levels[level]}`;
      } else {
        message = `You can't go lower, you are still at the bottom of Lader of Chance`;
      }
    } else {
      if (level < 4) {
        level = level + 1;
      }
      message = `You go higher to ${levels[level]}`;
    }

    this.setState({
      level: level,
    });

    return message;
  };

  winningBonus = () => {
    const bonuses = [
      {
        id: "turtle",
        message: "You gained Time Turtle!",
      },
      {
        id: "angel",
        message: "You won Angel Pass!",
      },
      {
        id: "monkey",
        message: "Hungry Monkey works for you!",
      },
    ];
    const result = random(0, 3);
    const bonus = bonuses[result];
    this.setState({
      ...this.state,
      bonuses: {
        ...this.state.bonuses,
        [bonus.id]: this.state.bonuses[bonus.id] + 1,
      },
    });

    return bonus.message;
  };

  getGoogleCard = () => {
    const card = random(0, 6);
    return googleCards[card];
  };

  hidePoints = () => {
    setTimeout(
      () =>
        this.setState({
          showPoints: false,
        }),
      700
    );
  };

  showPoints = () => {
    setTimeout(
      () =>
        this.setState({
          showPoints: true,
        }),
      1000
    );
  };

  onAnswerChange = (e) => {
    this.setState({
      value: e.target.value,
    });
  };

  handleKeyDown = (e, answer) => {
    if (e.key === "Enter") {
      this.validate(answer);
    }
  };

  spin = () => {
    const { rotation, wheelOfMayhem, points } = this.state;
    const field = random(0, 7);
    const spins = random(4, 7);
    const angle = spins * 360 + (360 / 7) * field;
    let message = "";

    if (rotation === angle) {
      this.spin();
    } else {
      this.audioRef.current.play();
      this.setState({
        rotation: angle,
      });
    }

    setTimeout(() => {
      switch (field) {
        case 0:
          const newPoints = Math.ceil(points / 2);
          this.setState({
            level: 0,
            points: newPoints,
            wheelOfMayhem: {
              ...wheelOfMayhem,
              questionType: "",
              message:
                "You have been bamboozled!!! Ross gets half of your points! And you go back to Lader of Chance.",
              sound: require("sounds/youvebeenbamboozled.mp3"),
              gif: require("img/happy_ross.gif"),
            },
          });
          break;
        case 1:
          message = this.winningBonus();
          this.setState({
            wheelOfMayhem: {
              ...wheelOfMayhem,
              questionType: "",
              message: message,
              sound: require("sounds/congratulations.mp3"),
              gif: require("img/congratulations.gif"),
            },
          });
          break;
        case 2:
          this.setState({
            wheelOfMayhem: {
              ...wheelOfMayhem,
              questionType: "quotation",
              message: "Who said that?",
              sound: require("sounds/said.mp3"),
              gif: require("img/shocked_chandler.gif"),
            },
          });
          break;
        case 3:
          this.setState({
            wheelOfMayhem: {
              ...wheelOfMayhem,
              questionType: "picture",
              message: "Do you know what you see?",
              sound: require("sounds/myEyes.mp3"),
              gif: require("img/my_eyes.gif"),
            },
          });
          break;
        case 4:
          this.setState({
            wheelOfMayhem: {
              ...wheelOfMayhem,
              questionType: "audio",
              message: "Do you know what you hear?",
              sound: require("sounds/singing.mp3"),
              gif: require("img/phoebe_confused.gif"),
            },
          });
          break;
        case 5:
          message = this.changeLevel();
          this.setState({
            wheelOfMayhem: {
              ...wheelOfMayhem,
              questionType: "",
              message: message,
              sound: require("sounds/wicked_wango.mp3"),
              gif: require("img/surprised.gif"),
            },
          });
          break;
        case 6:
          this.setState({
            wheelOfMayhem: {
              ...wheelOfMayhem,
              questionType: "video",
              message: "Finish Them!",
              sound: require("sounds/joey_arm.mp3"),
              gif: require("img/finish.gif"),
            },
          });
          break;
        default:
          break;
      }
    }, 2950);
  };

  hideButton = () => {
    this.setState({
      showButton: false,
    });
  };

  validate = (answer) => {
    let { value, question, gif, points } = this.state;
    const correct = answer.toLowerCase().trim() === value.toLowerCase().trim();
    points = correct ? points + 3 : points - 1;
    this.setState({ points: points, girlIdx: random(0, 8) });
    if (correct) {
      question++;
      if (question % 5 === 0 && question % 4 !== 0) {
        setTimeout(
          () =>
            this.setState({
              cards: true,
            }),
          2200
        );
      }

      if (question === 59) {
        this.setState({
          navigate: true,
        });
      }

      if (question % 4 === 0) {
        setTimeout(
          () =>
            this.setState({
              wheel: true,
            }),
          2200
        );
      }

      if (gif) {
        this.setState({
          result: correct,
          filled: true,
          question: question,
          value: "",
          gif: false,
          disableBonuses: false,
          hungryMonkey: false,
          enabledOptions: [],
        });
      } else {
        if (question % 2 === 0 && question % 5 !== 0 && question % 4 !== 0) {
          this.showPoints();
        }
        this.showPicture();
        this.hideButton();
        setTimeout(
          () =>
            this.setState({
              hungryMonkey: false,
              result: correct,
              filled: true,
              question: question,
              value: "",
              gif: false,
              disableBonuses: false,
              enabledOptions: [],
              showButton: true,
            }),
          2500
        );
      }
    } else {
      this.setState({
        result: false,
        filled: true,
        gif: false,
      });
    }
  };

  showPicture = () => {
    this.setState({
      gif: true,
    });
  };

  timeTurtle = () => {
    const { bonuses } = this.state;
    const turtle = bonuses.turtle - 1;
    this.showPicture();
    this.setState({
      disableBonuses: true,
      bonuses: {
        ...bonuses,
        turtle: turtle,
      },
    });
  };

  angelPass = () => {
    let { bonuses, question } = this.state;
    const angel = bonuses.angel - 1;
    question = question + 1;

    this.setState({
      question: question,
      bonuses: {
        ...bonuses,
        angel: angel,
      },
    });
  };

  hungryMonkey = () => {
    let { bonuses, question } = this.state;
    const monkey = bonuses.monkey - 1;
    const currentQuestion = questions[question];
    const enabledOptions = getEnabledOptions(
      currentQuestion.options,
      currentQuestion.answer
    );

    this.setState({
      hungryMonkey: true,
      disableBonuses: true,
      bonuses: {
        ...bonuses,
        monkey: monkey,
      },
      enabledOptions: enabledOptions,
    });
  };

  animationEnd = () => {
    this.setState({
      filled: false,
      result: false,
    });
  };

  showQuestions = (type) => {
    const { wheelOfMayhem } = this.state;

    if (type !== "") {
      let id = wheelOfMayhem[type];

      if (id > 8) {
        id = 0;
      } else {
        id = id + 1;
      }

      this.setState({
        ...this.state,
        rotation: 0,
        cards: false,
        wheel: false,
        wheelOfMayhem: {
          ...wheelOfMayhem,
          [type]: id,
          questionType: "",
          message: "",
          gif: "",
          sound: "",
        },
      });
    } else {
      this.setState({
        ...this.state,
        rotation: 0,
        cards: false,
        wheel: false,
        wheelOfMayhem: {
          ...wheelOfMayhem,
          questionType: "",
          message: "",
          gif: "",
          sound: "",
        },
      });
    }
  };

  render() {
    const {
      rotation,
      wheel,
      cards,
      value,
      result,
      filled,
      bonuses,
      question,
      gif,
      disableBonuses,
      hungryMonkey,
      enabledOptions,
      showPoints,
      points,
      girlIdx,
      wheelOfMayhem,
      navigate,
      showButton,
    } = this.state;

    const error = !result && filled;
    return (
      <Container>
        {!cards && !wheel && (
          <>
            <Question
              query={questions[question]}
              onAnswerChange={this.onAnswerChange}
              value={value}
              validate={this.validate}
              showPicture={gif}
              hungryMonkey={hungryMonkey}
              enabledOptions={enabledOptions}
              error={error}
              animationEnd={this.animationEnd}
              handleKeyDown={this.handleKeyDown}
              showButton={showButton}
            />
            <Bonus
              disabled={disableBonuses}
              count={bonuses}
              turtleClick={this.timeTurtle}
              angelClick={this.angelPass}
              monkeyClick={this.hungryMonkey}
            />
          </>
        )}
        {showPoints && (
          <Bikini score={points} idx={girlIdx} hide={this.hidePoints} />
        )}
        {cards && (
          <Cards
            wangoAction={this.changeLevel}
            saucerAction={this.winningBonus}
            googleAction={this.getGoogleCard}
            goBack={this.showQuestions}
          />
        )}
        {wheel && (
          <WheelOfMayhem
            rotation={rotation}
            audio={this.audioRef}
            onClick={this.spin}
            data={wheelOfMayhem}
            goBack={this.showQuestions}
          />
        )}
        {navigate && <Navigate replace to="/end" />}
      </Container>
    );
  }
}

export default Quiz;
