import { Container, Character } from "./styled";
function Characters() {
  return (
    <Container>
      {[...Array(7).keys()].map((_, i) => {
        return (
          <Character
            src={require(`img/characters/character${i + 1}.gif`)}
            id={i}
            key={i}
          />
        );
      })}
    </Container>
  );
}

export default Characters;
