import { Component } from "react";
import { Container, ButtonContainer } from "./styled";
import Card from "./Card";
import Button from "components/Button";
import back from "img/back.png";
import wango from "img/wicked_wango.png";
import google from "img/google.png";
import saucer from "img/saucer_card.png";

class Cards extends Component {
  constructor(props) {
    super(props);

    this.state = {
      wangoActive: false,
      googleActive: false,
      saucerActive: false,
      wangoMessage: "",
      googleBack: null,
    };
  }

  wangoCard = (e) => {
    this.setState({
      wangoActive: true,
      wangoMessage: this.props.wangoAction(),
    });
  };
  googleCard = () => {
    this.setState({
      googleActive: true,
      googleBack: this.props.googleAction(),
    });
  };
  saucerCard = () => {
    this.setState({
      saucerActive: true,
      saucerMessage: this.props.saucerAction(),
    });
  };

  render() {
    const {
      wangoActive,
      googleActive,
      saucerActive,
      wangoMessage,
      saucerMessage,
      googleBack,
    } = this.state;
    const showButton = wangoActive || googleActive || saucerActive;
    return (
      <Container>
        <Card
          id={0}
          img={wango}
          action={this.wangoCard}
          active={wangoActive}
          back={wangoMessage}
          disabled={googleActive || saucerActive}
        />
        <Card
          id={1}
          img={google}
          action={this.googleCard}
          active={googleActive}
          items={googleBack}
          disabled={wangoActive || saucerActive}
        />
        <Card
          id={2}
          img={saucer}
          action={this.saucerCard}
          active={saucerActive}
          back={saucerMessage}
          disabled={wangoActive || googleActive}
        />
        <ButtonContainer>
          {showButton && (
            <Button label={back} action={() => this.props.goBack("")} />
          )}
        </ButtonContainer>
      </Container>
    );
  }
}

export default Cards;
