import { Image, Container, BigImage } from "./styled";
import { Question, Radio, Label } from "../styled";

const Picture = ({
  question,
  picture,
  options,
  onAnswerChange,
  value,
  showButton,
}) => {
  return (
    <div>
      <Question>{question}</Question>
      <Container>
        <Image src={picture} />
        <BigImage src={picture} />
      </Container>
      {options.map((option) => {
        return (
          <Radio key={option}>
            <input
              onChange={(e) => {
                onAnswerChange(e);
                showButton();
              }}
              type="radio"
              value={option}
              checked={option === value}
              id={option}
            />
            <Label htmlFor={option}>{option}</Label>
          </Radio>
        );
      })}
    </div>
  );
};

export default Picture;
