import { Head } from "./styled";
import head from "img/flying_head.png";

function Heads() {
  return (
    <>
      <Head src={head} className="first" />
      <Head src={head} className="second" />
      <Head src={head} className="third" />
      <Head src={head} className="fourth" />
    </>
  );
}

export default Heads;
