import styled, { keyframes } from "styled-components";

const peekABoo = (side) => keyframes`
  0% {
    ${side}: -300px;
  }

  50% {
    ${side}: 0;
  }

  100% {
    ${side}: -300px;
  }
`;

export const Head = styled.img`
  position: absolute;
  width: 300px;

  &.first {
    bottom: 0;
    animation: 2s ${peekABoo("right")};
    animation-delay: 12.5s;
    animation-fill-mode: both;
  }

  &.second {
    animation: 2s ${peekABoo("right")};
    animation-delay: 20s;
    animation-fill-mode: both;
    top: 75px;
  }

  &.third {
    animation: 2s ${peekABoo("left")};
    top: 150px;
    animation-delay: 25s;
    animation-fill-mode: both;
    transform: scaleX(-1);
  }

  &.fourth {
    animation: 2s ${peekABoo("top")};
    animation-delay: 40s;
    animation-fill-mode: both;
    transform: rotate(-90deg);
  }
`;
