import { Component } from "react";
import { Navigate } from "react-router-dom";
import { Wrapper, Container, Input, Right, Wrong } from "./styled";
import wrong from "img/wrong.webp";
import right from "img/right.png";

const inputs = [
  {
    name: "first",
    symbol: "d",
  },
  {
    name: "second",
    symbol: "u",
  },
  {
    name: "third",
    symbol: "p",
  },
  {
    name: "fourth",
    symbol: "a",
  },
  {
    name: "fifth",
    symbol: "l",
  },
];

class Cryptex extends Component {
  constructor(props) {
    super(props);

    this.state = {
      result: false,
      filled: false,
      animationCounter: 0,
      values: {},
      navigate: false,
    };
  }

  onChange = (e) => {
    const { values } = this.state;
    values[e.target.name] = e.target.value;
    this.setState({
      ...this.state,
      values,
    });
    this.validate();
  };

  validate = () => {
    const { values } = this.state;
    const correct = inputs.filter(
      (input) => input.symbol === this.state.values[input.name]
    );

    this.setState({
      filled: Object.keys(values).length === 5,
      result: correct.length === 5,
    });
  };

  countAnimations = () => {
    const { animationCounter } = this.state;
    this.setState({
      animationCounter: animationCounter + 1,
    });
  };

  hide = (e) => {
    const { animationCounter } = this.state;

    if (animationCounter === 2) {
      this.setState({
        filled: false,
      });
    }
  };

  redirectToGame = () => {
    setTimeout(() => this.setState({ navigate: true }), 2000);
  };

  render() {
    const { filled, result, navigate } = this.state;
    return (
      <Wrapper>
        {filled && !result && (
          <Wrong
            src={wrong}
            onAnimationEnd={this.hide}
            onAnimationStart={this.countAnimations}
          />
        )}
        <Container>
          {inputs.map((input, i) => {
            return (
              <Input
                key={input.name}
                type="text"
                placeholder="?"
                onChange={(e) => this.onChange(e)}
                value={this.state.values[input.name]}
                name={input.name}
                maxLength="1"
                autoFocus={i === 0}
              />
            );
          })}
        </Container>
        {filled && result && (
          <Right src={right} onAnimationEnd={this.redirectToGame} />
        )}
        {navigate && <Navigate replace to="/game/rules" />}
      </Wrapper>
    );
  }
}

export default Cryptex;
