import { Container, Image, Text } from "./styled";

const pictures = [
  {
    img: require("img/bikini/bikini1.png"),
    top: "215px",
    left: "450px",
    rotation: "-2deg",
    text: (points) => `Who scored ${points}? <br/><br/> YOU DID`,
  },
  {
    img: require("img/bikini/bikini2.png"),
    top: "220px",
    left: "420px",
    rotation: "3deg",
    text: (points) => `${points} points <br/> for you`,
  },
  {
    img: require("img/bikini/bikini3.png"),
    top: "190px",
    left: "315px",
    rotation: "0deg",
    text: (points) =>
      `You have already scored ${points} points of awesomeosity`,
  },
  {
    img: require("img/bikini/bikini4.png"),
    top: "125px",
    left: "520px",
    rotation: "-5deg",
    text: (points) => `You've got <br> ${points} points`,
  },
  {
    img: require("img/bikini/bikini5.png"),
    top: "165px",
    left: "530px",
    rotation: "-2deg",
    text: (points) => `Current <br>score: <br> ${points}`,
  },
  {
    img: require("img/bikini/bikini6.png"),
    top: "260px",
    left: "600px",
    rotation: "-1deg",
    text: (points) => `Your awesome <br>result is <br> ${points}`,
  },
  {
    img: require("img/bikini/bikini7.png"),
    top: "200px",
    left: "490px",
    rotation: "31deg",
    text: (points) => `Congratulations! <br > You scored ${points}`,
  },
  {
    img: require("img/bikini/bikini8.png"),
    top: "185px",
    left: "560px",
    rotation: "-4deg",
    text: (points) => `Your <br> current <br> score <br>is<br> ${points}`,
  },
];

export const Bikini = ({ score, idx, hide }) => {
  return (
    <Container onAnimationEnd={hide}>
      <Image src={pictures[idx].img} />
      <Text
        position={pictures[idx]}
        dangerouslySetInnerHTML={{
          __html: pictures[idx].text(score),
        }}
      ></Text>
    </Container>
  );
};

export default Bikini;
