import { Player } from "./styled";
import { Question, Radio, Label } from "../styled";

const Sound = ({
  question,
  audio,
  options,
  onAnswerChange,
  value,
  showButton,
}) => {
  return (
    <div>
      <Question>{question}</Question>
      <Player src={audio} autoPlay controls onEnded={showButton} />
      {options.map((option) => {
        return (
          <Radio key={option}>
            <input
              onChange={onAnswerChange}
              type="radio"
              value={option}
              checked={option === value}
              id={option}
            />
            <Label htmlFor={option}>{option}</Label>
          </Radio>
        );
      })}
    </div>
  );
};

export default Sound;
