import styled, { keyframes, css } from "styled-components";
import checkmark from "img/checkmark.png";
import { shake } from "react-animations";

const shakeAnimation = keyframes`${shake}`;
const redBorder = keyframes`
  0%, 100% {
    border: 1px solid black;
  }
  50% {
    border: 2px solid red;
  }
`;

export const Container = styled.div`
  margin-bottom: 30px;
  display: flex;
  width 100%;
  flex: 1;
`;

export const Content = styled.div`
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
`;

export const ImageContent = styled.div`
  width: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
`;

export const Text = styled.p`
  font-size: 25px;
  text-align: center;
  height: 100px;
  margin: 0;
  padding: 10px 0;
`;

export const Field = styled.div`
  min-height: 240px;
  margin-bottom: 10px;
`;

export const Input = styled.input.attrs({ type: "text" })`
  height: 50px;
  padding: 10px;
  border-radius: 10px;
  outline: none;
  border: 1px solid black;
  display: block;
  margin: auto;
  margin-bottom: 30px;
  min-width: 250px;
  animation: ${(props) =>
    props.error ? css`1s ${redBorder}, 1s ${shakeAnimation}` : "none"};
`;

export const Label = styled.label`
  display: block;
  padding-left: 25px;
  position: relative;
  cursor: pointer;
  display: flex;
  align-items: center;
  font-size: 20px;

  &:before {
    content: " ";
    width: 15px;
    height: 15px;
    border: 1px solid black;
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
  }
`;

export const Options = styled.div`
  width: 400px;
  animation: ${(props) => (props.error ? css`1s ${shakeAnimation}` : "none")};

  ${Label} {
    &:before {
      animation: ${(props) => (props.error ? css`1s ${redBorder}` : "none")};
    }
  }
`;

export const Radio = styled.div`
  display: block;
  line-height: 1;
  margin-bottom: 10px;

  input {
    display: none;

    &:disabled {
      & + ${Label} {
        opacity: 0.5;
      }
    }

    &:checked {
      & + ${Label} {
        &:after {
          content: " ";
          position: absolute;
          width: 15px;
          height: 15px;
          top: 50%;
          transform: translateY(-50%);
          left: 0;
          display: flex;
          justify-content: center;
          align-items: center;
          text-align: center;
          background-color: #00b4e9;
          color: white;
          font-size: 14px;
          border: 1px solid #00b4e9;
          background-image: url(${checkmark});
          background-size: contain;
          background-repeat: no-repeat;
          background-position: center;
        }
      }
    }
  }
`;
export const Image = styled.img`
  max-width: 450px;
  min-width: 300px;
  max-height: 300px;
  z-index: 20;
`;

export const ButtonContainer = styled.div`
  margin-top: auto;
`;
