import styled, { keyframes, css } from "styled-components";
import { bounceInUp } from "react-animations";

const bounceInUpAnimation = keyframes`${bounceInUp}`;

const sides = `
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  top: 0;
  left: 0;  
`;

export const Box = styled.div`
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.6s;
  transform-style: preserve-3d;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
`;

export const Container = styled.div`
  width: 180px;
  height: 257px;
  perspective: 1000px;
  background-color: transparent;
  cursor: pointer;
  animation: 0.5s ${bounceInUpAnimation};
  animation-delay: ${(props) => props.id * 0.5}s;
  animation-fill-mode: backwards;
  pointer-events: ${(props) =>
    props.disabled || props.active ? "none" : "auto"};
  filter: grayscale(${(props) => (props.disabled ? "100%" : "0%")});

  ${Box} {
    transform: ${(props) => (props.active ? css`rotateY(-180deg)` : "none")};
  }
`;

export const CardFront = styled.img`
  ${sides}
`;

export const CardBack = styled.div`
  ${sides}
  border: 10px solid black;
  background-color: white;
  transform: rotateY(-180deg);
  border-radius: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Internet Friends";
  font-size: 20px;
  padding: 20px;
`;

export const GoogleBack = styled.div`
  ${sides}
  border: 10px solid black;
  background-color: white;
  transform: rotateY(-180deg);
  border-radius: 15px;
  display: flex;
  font-family: "Internet Friends";
  padding: 5px;
  flex-direction: column;
  justify-content: space-between;
`;

export const Header = styled.p`
  font-size: 22px;
  margin: 5px 0 10px;
`;

export const Description = styled.p`
  font-size: 15px;
  margin: 10px 0;
`;

export const Image = styled.img`
  max-width: 100%;
  margin: auto;
`;
