import { Friend, Container, Wrapper } from "./styled";
import { Question } from "../styled";
import rachel from "img/rachel.jpg";
import monica from "img/monica.webp";
import phoebe from "img/phoebe.jpg";
import joey from "img/joey.webp";
import chandler from "img/chandler.jpg";
import ross from "img/ross.jpg";

const friends = [
  {
    bg: rachel,
    value: "rachel",
  },
  {
    bg: monica,
    value: "monica",
  },
  {
    bg: phoebe,
    value: "phoebe",
  },
  {
    bg: joey,
    value: "joey",
  },
  {
    bg: chandler,
    value: "chandler",
  },
  {
    bg: ross,
    value: "ross",
  },
];

const Quotation = ({ question, value, onAnswerChange, showButton }) => {
  return (
    <Wrapper>
      <Question>{question}</Question>
      <Container>
        {friends.map((f) => (
          <Friend
            key={f.value}
            bg={f.bg}
            value={f.value}
            onClick={(e) => {
              onAnswerChange(e);
              showButton();
            }}
            selected={f.value === value}
          />
        ))}
      </Container>
    </Wrapper>
  );
};

export default Quotation;
