import React, { Component } from "react";
import { Navigate } from "react-router-dom";
import { Container, Text, Joey, Wrapper, Review } from "./styled";
import lets from "img/lets.png";
import play from "img/play.png";
import bamboozled from "img/bamboozled.png";
import joey from "img/joey.png";
import audio from "sounds/bamboozled.mp3";
import review from "img/review.png";

class Start extends Component {
  constructor(props) {
    super(props);
    this.audioRef = React.createRef();
    this.state = {
      navigate: false,
      animationCounter: 0,
    };
  }
  autoplay = () => {
    this.audioRef.current.play();
  };

  componentDidMount() {
    setTimeout(this.autoplay, 1200);
  }

  redirectToQuiz = () => {
    this.setState({
      navigate: true,
    });
  };

  countAnimations = () => {
    this.setState((prevState) => {
      return {
        animationCounter: prevState.animationCounter + 1,
      };
    });
  };

  handleEnd = () => {
    const { animationCounter } = this.state;
    if (animationCounter === 2) {
      this.redirectToQuiz();
    }
  };

  render() {
    const { navigate } = this.state;
    return (
      <Container>
        <Wrapper>
          <audio
            id="player"
            src={audio}
            onEnded={this.redirectToQuiz}
            ref={this.audioRef}
          />
          <Joey
            src={joey}
            onAnimationStart={this.countAnimations}
            onAnimationEnd={this.handleEnd}
          />
          <Text src={lets} id={0} />
          <Text src={play} id={1} />
          <Text src={bamboozled} id={2} />
        </Wrapper>
        {navigate && <Navigate replace to="/game/animations" />}
        <Review src={review} />
      </Container>
    );
  }
}

export default Start;
