import styled, { keyframes } from "styled-components";
import { bounceInUp, bounceOutDown } from "react-animations";
import left from "img/cryptex_left.png";
import right from "img/cryptex_right.png";
import texture from "img/texture.png";

export const Wrapper = styled.div`
  width: 100%;
  display: flex;
`;

export const Container = styled.div`
  display: flex;
  height: 115px;
  width: 300px;
  margin: 30px auto;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;

  @media (max-height: 800px) {
    margin: 5px auto;
  }

  &:before {
    content: " ";
    height: 115px;
    width: 100px;
    z-index: 2;
    position: absolute;
    top: 0;
    left: 0;
    background-image: url(${left});
    background-size: contain;
    pointer-events: none;
  }

  &:after {
    content: " ";
    height: 115px;
    width: 100px;
    z-index: 2;
    position: absolute;
    top: 0;
    right: 0;
    background-image: url(${right});
    background-size: contain;
    pointer-events: none;
  }
`;

export const Input = styled.input.attrs({ type: "text" })`
  max-width: 31px;
  height: 105px;
  background-image: url(${texture});
  background-size: cover;
  border: none;
  border-right: 1px solid saddlebrown;
  text-align: center;
  outline: none;
  transition: opacity 0.25s ease-out;

  &:focus {
    opacity: 0.9;
  }

  ::placeholder {
    color: black;
  }
`;

const bounceInUpAnimation = keyframes`${bounceInUp}`;
const bounceOutDownAnimation = keyframes`${bounceOutDown}`;

export const Wrong = styled.img`
  max-width: 200px;
  height: auto;
  position: absolute;
  left: 150px;
  bottom: 0;
  animation: 1s ${bounceInUpAnimation}, 1s ${bounceOutDownAnimation};
  animation-delay: 0s, 2s;
  animation-fill-mode: backwards, forwards;
`;

export const Right = styled.img`
  max-width: 200px;
  height: auto;
  position: absolute;
  right: 150px;
  bottom: 50px;
  animation: 1s ${bounceInUpAnimation};

  @media (max-height: 800px) {
    bottom: 30px;
  }
`;
