import { Component } from "react";
import { questions } from "data/animationQuestions";
import { Container, Question, Text } from "./styled";
import Radiobutton from "./Radiobutton";
import Cryptex from "./Cryptex";
import Hint from "./Hint";

class Questions extends Component {
  constructor(props) {
    super(props);
    this.state = {
      answers: {},
      cryptex: false,
    };
  }

  onAnswerChange = (e) => {
    const { name, value } = e.target;
    const { answers } = this.state;
    answers[name] = value;
    const cryptex = Object.keys(answers).length === 6;

    this.setState({
      answers: answers,
      cryptex: cryptex,
    });
  };

  render() {
    const { answers, cryptex } = this.state;

    return (
      <Container>
        {questions.map((item) => {
          const { id, options, question, hint } = item;
          return (
            <Question key={id}>
              <Text>
                {id}. {question}
              </Text>
              {Object.keys(options).map((name) => {
                return (
                  <Radiobutton
                    key={name}
                    name={name}
                    onAnswerChange={this.onAnswerChange}
                    options={options}
                    id={id}
                    answers={answers}
                  />
                );
              })}
              {hint && <Hint image={hint} />}
            </Question>
          );
        })}
        {cryptex && <Cryptex />}
      </Container>
    );
  }
}

export default Questions;
